.test {
	display: grid;
	grid-template-columns: 100%;
	grid-template-rows: minmax(auto, 490px) auto;
	border-radius: 10px;
	box-shadow: 1px 1px 28px 10px gray;
	border: 1px solid gray;
	margin: 20px;
	padding: 10px;
}

.test__error {
	box-shadow: 1px 1px 28px 10px red;
}
.test__sucsess {
	box-shadow: 1px 1px 28px 10px greenyellow;
}
.test__info {
	border-radius: 10px;
	position: relative;
	margin-bottom: 10px;
	margin: 5px 5px;
}
.test__img {
	width: 100%;
	height: 100%;
	position: absolute;
	padding-bottom: 50%;
	z-index: -1;
	overflow: hidden;
	border-radius: 10px;
}
.test__img img {
	width: 100%;
	height: 100%;
	object-fit: cover;
}
.test__counters {
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
	gap: 10px;
	padding: 20px;
	font-weight: bold;
	font-size: x-large;
	align-items: center;
}
.test__timer,
.test__result,
.test__question,
.test__message,
.test__results {
	padding: 10px;
	border-radius: 10px;
	background-color: rgba(0, 120, 201, 0.9);
	text-align: center;
	color: white;
}
.test__timer {
	cursor: pointer;
}
.test__question,
.test__message {
	width: 92%;
	margin: auto;
	font-size: xx-large;
	font-weight: bold;
}
.test__answers {
	display: grid;
	grid-template-columns: repeat(2, 50%);
	grid-template-rows: repeat(2, auto);
}
.test__finish,
.test__start {
	max-width: 100%;
	text-align: center;
}
.test__finish button {
	color: red;
	font-weight: bold;
}
.test__start button {
	font-weight: bold;
	color: green;
}

.button {
	margin: 5px;
}
.button :hover {
	background-color: #008cba;
	color: white;
}
.button button {
	width: 100%;
	font-size: x-large;
	border-radius: 10px;
	border: 1px solid gray;
	height: 50px;
	background-color: white;
	cursor: pointer;
}
.correctAnswer button {
	background-color: greenyellow;
}

@media (max-width: 767px) {
	.test {
		grid-template-rows: minmax(auto, 360px) auto;
		margin: 10px;
		padding: 10px;
		border: none;
	}
	.test__answers {
		display: grid;
		grid-template-columns: auto;
	}
	.test__question,
	.test__message {
		font-size: large;
		font-weight: bold;
	}
	.test__counters {
		font-size: large;
		align-items: center;
	}
	.button button {
		font-size: large;
		height: 40px;
	}
}

@media (max-width: 480px) {
	.test {
		grid-template-rows: minmax(auto, 360px) auto;
		margin: 10px;
		padding: 10px;
		border: none;
	}
	.test__answers {
		display: grid;
		grid-template-columns: auto;
	}
	.test__question,
	.test__message {
		font-size: large;
		font-weight: bold;
	}
	.test__counters {
		font-size: large;
		align-items: center;
	}
	.button button {
		font-size: large;
		height: 40px;
	}
	.test__result,
	.test__results {
		font-size: smaller;
	}
}
@media (max-width: 375px) {
	.test {
		grid-template-rows: minmax(auto, 270px) auto;
		margin: 10px;
		border: none;
	}
	.test__answers {
		display: grid;
		grid-template-columns: auto;
	}
	.test__question,
	.test__message {
		font-size: large;
		font-weight: bold;
	}
	.test__counters {
		font-size: large;
		align-items: center;
	}
	.button button {
		font-size: large;
		height: 40px;
	}
	.test__result,
	.test__results {
		font-size: small;
	}
}
